/** view - Í≥µÌÜµ ÏöîÏÜå */
export default abstract class View {
    private template: string;
    private renderTemplate: string;
    private container: HTMLElement;
    private htmlList: string[]; // empty array -> html list append

    constructor(containerId: string, template: string) {
        const containerElement: HTMLElement | null = document.getElementById(containerId);

        if (!containerElement) {
            throw 'ÏµúÏÉÅÏúÑ Ïª®ÌÖåÏù¥ÎÑàÍ∞Ä ÏóÜÏñ¥ UIÎ•º ÏßÑÌñâÌïòÏßÄ Î™ªÌï©ÎãàÎã§.'
        }

        this.container = containerElement;
        this.template = template;
        this.renderTemplate = template;
        this.htmlList = [];
    }

    protected updateView(): void {
        this.container.innerHTML= this.renderTemplate;
        this.renderTemplate = this.template; // UI ÏóÖÎç∞Ïù¥Ìä∏ ÏûëÏóÖ ÌõÑ ÏõêÎ≥∏ ÌÖúÌîåÎ¶øÏúºÎ°ú Î≥µÏõê
    }

    /** html Î¨∏ÏûêÏó¥ Ï∂îÍ∞Ä Ìï®Ïàò */
    protected addHtml(htmlString: string): void {
        this.htmlList.push(htmlString);
    }

    /** Î¨∏ÏûêÏó¥ Î≥ëÌï© Í∞í Î¶¨ÌÑ¥ Ìï®Ïàò */
    protected getHtml(): string {
        const snapshot = this.htmlList.join('');
        this.clearHtmlList(); // html list clear
        return snapshot;
    }

    /** template ÎÇ¥Ïö© ÎåÄÏ≤¥ Ìï®Ïàò */
    protected setTemplateData(key: string, value: string) {
        this.renderTemplate = this.renderTemplate.replace(`{{__${key}__}}`, value);
    }

    /** html list clear Ìï®Ïàò */
    private clearHtmlList(): void {
        this.htmlList = [];
    }

    abstract render(...params: string[]): void; // abstract method
}